<template>
  <div class="Login">
    <div class="wrapper">
      <div class="title">Вход</div>
      <el-form
        ref="form "
        :model="sign_in">
        <el-form-item><el-input
          v-model="sign_in.email"
          placeholder="Логин" /></el-form-item>
        <el-form-item><el-input
          v-model="sign_in.password"
          placeholder="Пароль" /></el-form-item>
        <el-button
          type="primary"
          @click="login">Войти</el-button>
      </el-form>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Login',

  metaInfo: {
    title: 'Вход в систему',
  },

  data() {
    return {
      sign_in: {
        email: 'admin@demo.com',
        password: 'admin',
      },
    };
  },

  methods: {
    login() {
      this.$store.dispatch('auth/login', this.sign_in);
    },
  },
};
</script>

